































































import { Vue, Component } from 'vue-property-decorator';

import { EFormType, EFormMode } from '@/../enums';
import { ServiceRegex, ServiceToast, ServiceResponseFilter, ServiceServerConfig, ServiceFormConfig, EUserRole, StorageUser } from '@/../helpers';
import { IComponent } from '@/../models';

import { ServerApiPath } from '@/config';

import Loading from '@/../components/loading/loading.vue';
import Forms from '@/../components/form/forms.vue';
import Card from '@/../components/card/card.vue';

interface ISelectOptionItem {}

interface IParam {
    objectId: string;
    name: string;
    email: string;
    position: string;
    phone: string;
}

interface IConfig {
    username: string;
    role: EUserRole;
    name: string;
    email: string;
    position: string;
    phone: string;
}

// form
interface IFormItem {
    isClearState: boolean;
}

interface IFormData {
    objectId: string;
    username: string;
    role: string;
    name: string;
    email: string;
    position: string;
    phone: string;
}

interface IFormConfigs {
    username: IComponent.IFormConfig;
    role: IComponent.IFormConfig;
    name: IComponent.IFormConfig;
    email: IComponent.IFormConfig;
    position: IComponent.IFormConfig;
    phone: IComponent.IFormConfig;
}

interface IFormDataDisabled {}

interface IFormHidden {}

interface IFormButtonDisabled {
    name: boolean;
    position: boolean;
    phone: boolean;
}

@Component({
    components: { Forms, Card },
})
export default class VuePageClass extends Vue {
    // select option
    private selectOptionItem: ISelectOptionItem = {};

    private formItem: IFormItem = {
        isClearState: true,
    };

    private formDataOriginal: IFormData = {
        objectId: '',
        username: '',
        role: '',
        name: '',
        email: '',
        position: '',
        phone: '',
    };
    private formData: IFormData = { ...this.formDataOriginal };

    private formDataDisabledOriginal: IFormDataDisabled = {};
    private formDataDisabled: IFormDataDisabled = { ...this.formDataDisabledOriginal };

    private formConfigOriginal: IFormConfigs = {
        username: JSON.parse(JSON.stringify(ServiceFormConfig.getFormConfig())),
        name: JSON.parse(JSON.stringify(ServiceFormConfig.getFormConfig())),
        email: JSON.parse(JSON.stringify(ServiceFormConfig.getFormConfig())),
        role: JSON.parse(JSON.stringify(ServiceFormConfig.getFormConfig())),
        position: JSON.parse(JSON.stringify(ServiceFormConfig.getFormConfig())),
        phone: JSON.parse(JSON.stringify(ServiceFormConfig.getFormConfig())),
    };
    private formConfig: IFormConfigs = { ...this.formConfigOriginal };
    private formConfigs: IComponent.IFormConfig[] = [];

    private formTestConfigs: IComponent.IFormConfig[] = [];

    private formButtonDisabled: IFormButtonDisabled = {
        name: true,
        position: false,
        phone: false,
    };

    private formHidden: IFormHidden = {};

    private eFormMode = EFormMode;
    private loading = new Loading();

    //////////////////// Vue Life ////////////////////

    private async beforeCreate(): Promise<void> {}
    private async created(): Promise<void> {}
    private async beforeMount(): Promise<void> {}
    private async mounted(): Promise<void> {
        this.initFormConfig();
        this.initFormData();
    }
    private async beforeDestroy(): Promise<void> {}
    private async destroyed(): Promise<void> {}

    //////////////////// Init Data ////////////////////

    private initFormConfig(): void {
        this.formConfigs = [];

        // username
        this.formConfig.username.id = 'username';
        this.formConfig.username.label = this.$i18n.Common_Account;
        this.formConfig.username.placeholder = this.$i18n.Pleaceholder_Account;
        this.formConfig.username.formType = EFormType.label;

        // role
        this.formConfig.role.id = 'role';
        this.formConfig.role.label = this.$i18n.Common_Role;
        this.formConfig.role.placeholder = this.$i18n.Common_Role;
        this.formConfig.role.formType = EFormType.label;

        // name
        this.formConfig.name.id = 'name';
        this.formConfig.name.label = this.$i18n.Common_User_Name;
        this.formConfig.name.placeholder = this.$i18n.Common_User_Name;
        this.formConfig.name.formType = EFormType.text;
        this.formConfig.name.required = true;

        // email
        this.formConfig.email.id = 'email';
        this.formConfig.email.label = this.$i18n.Common_Email;
        this.formConfig.email.placeholder = this.$i18n.Placeholder_Email;
        this.formConfig.email.formType = EFormType.text;
        this.formConfig.email.regex = ServiceRegex.email();
        this.formConfig.email.regexErrorTip = this.$i18n.Error_Format_Email;
        this.formConfig.email.required = true;

        // job position
        this.formConfig.position.id = 'position';
        this.formConfig.position.label = this.$i18n.Account_Table_Job_Position;
        this.formConfig.position.placeholder = this.$i18n.Account_Table_Job_Position;
        this.formConfig.position.formType = EFormType.text;
        this.formConfig.position.required = false;

        // phone
        this.formConfig.phone.id = 'phone';
        this.formConfig.phone.label = this.$i18n.Account_Table_Phone;
        this.formConfig.phone.placeholder = this.$i18n.Placeholder_Phone;
        this.formConfig.phone.formType = EFormType.text;
        this.formConfig.phone.regex = /(^\+{1}[0-9]+$)|(^[\d]+$)/; // 只允許 + 與 數字
        this.formConfig.phone.regexErrorTip = this.$i18n.Error_Format_Phone;

        this.formConfigs = [
            this.formConfig.username,
            this.formConfig.role,
            this.formConfig.name,
            this.formConfig.email,
            this.formConfig.position,
            this.formConfig.phone,
        ];
    }

    private async initFormData() {
        let user: any = this.$user?.user ?? null;

        setTimeout(() => this.formDataSet(user), 0);
    }

    //////////////////// Init Select Option  ////////////////////
    private async initSelectOption(): Promise<void> {}

    //////////////////// Form Show ////////////////////

    private formSaveDisable(): boolean {
        return Object.values(this.formButtonDisabled).some((x) => x === true);
    }

    //////////////////// Form Set Data ////////////////////

    private formDataClear(): void {
        this.formData = JSON.parse(JSON.stringify({ ...this.formDataOriginal }));
    }

    private formConfigValues(value: any, disabledBtuuon: boolean, id: string): void {
        Object.keys(this.formData).forEach((key) => {
            if (id === key) this.formData[id] = value;
        });

        Object.keys(this.formButtonDisabled).forEach((key) => {
            if (id === key) this.formButtonDisabled[id] = disabledBtuuon;
        });
    }

    private formDisabledValues(value: boolean, id: string): void {
        Object.keys(this.formDataDisabled).forEach((key) => {
            if (id === key) this.formDataDisabled[id] = value;
        });
    }

    private formDataSet(data: any): void {
        // edit
        this.formData.objectId = data.objectId ? data.objectId : '';
        this.formData.username = data.username ? data.username : '';
        this.formData.role = data.roles[0]?.name;
        this.formData.name = data.name ? data.name : '';
        this.formData.email = data.email ? data.email : '';
        this.formData.position = data.position ? data.position : '';
        this.formData.phone = data.phone ? data.phone : '';
    }

    //////////////////// Modify API (save, delete, test)  ////////////////////
    private async dataSave(): Promise<void> {
        let data: IFormData = this.formData;

        interface IDataSaveParam {
            name: string;
            email: string;
            position?: string;
            phone?: string;
        }

        let param: IDataSaveParam = {
            name: data.name,
            email: data.email,
        };

        if (data.position) param.position = data.position;

        if (data.phone) param.phone = data.phone;

        this.loading.show();

        this.loading.hide();
        this.formItem.isClearState = false;
        ServiceToast.success(this.$i18n.Dialog_SaveSuccess);

        this.$user.user.email = param.email;
        this.$user.user.phone = param.phone;
        this.$user.user.position = param.position;
        this.$user.user.name = param.name;

        StorageUser.saveStorage(this.$user);
    }

    //////////////////// Resolve text ////////////////////
    private resolveRole(value: string): string {
        let result: string = '';
        switch (value) {
            case EUserRole.SystemAdministrator:
                result = this.$i18n.Role_SystemAdministrator;
                break;
            case EUserRole.Administrator:
                result = this.$i18n.Role_Administrator;
                break;
            case EUserRole.Manager:
                result = this.$i18n.Role_Manager;
                break;
        }
        return result;
    }
}
